import { State } from './../../common/state';
import { Country } from './../../common/country';
import { FormsService } from './../../services/forms.service';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { IfStmt } from '@angular/compiler';

@Component({
  selector: 'app-checkout',
  templateUrl: './checkout.component.html',
  styleUrls: ['./checkout.component.css']
})
export class CheckoutComponent implements OnInit {

  checkoutFormGroup: FormGroup;
  totalQuantity: number;
  totalPrice: number;
  creditCardMonths: number[] = [];
  creditCardYear: number[] = [];
  countries: Country[] = [];
  shippingAddressStates: State[] = [];
  billingAddressStates: State[] = [];
  constructor(private formBuilder: FormBuilder, private formService: FormsService) { }

  ngOnInit(): void {
    let startMonth = new Date().getMonth() + 1;
    this.formService.getCreditCardMonths(startMonth).subscribe(
      thedata => this.creditCardMonths = thedata
    );
    this.formService.getCreditCardYears().subscribe(
      data => this.creditCardYear = data
    );
    this.checkoutFormGroup = this.formBuilder.group({
      customer: this.formBuilder.group({
        firstname: [''],
        lastname: [''],
        email: ['']
      }),
      shippingAddress: this.formBuilder.group({
        country: [''],
        street: [''],
        city: [''],
        state: [''],
        zipcode: ['']
      }),
      billingAddress: this.formBuilder.group({
        country: [''],
        street: [''],
        city: [''],
        state: [''],
        zipcode: ['']
      }),
      creditCard: this.formBuilder.group({
        cardType: [''],
        nameOnCard: [''],
        cardNumber: [''],
        securityCode: [''],
        expirationMonth: [''],
        expirationYear: ['']
      })
    });

    this.formService.getCountryList().subscribe(
      data => this.countries = data
    )
  }

  copyShippingToBilling(event) {

    if (event.target.checked) {
      this.checkoutFormGroup.controls.billingAddress.setValue(this.checkoutFormGroup.controls.shippingAddress.value);
      this.billingAddressStates=this.shippingAddressStates;
    }
    else {
      this.checkoutFormGroup.controls.billingAddress.reset();
      this.billingAddressStates=[];
    }

  }

  handleMonthandYearChange() {
    let currentFormGroup = this.checkoutFormGroup.get("creditCard");
    let currentYear = new Date().getFullYear();
    let selectedYear = currentFormGroup.value.expirationYear;
    let startMonth: number;
    if (currentYear === selectedYear) {
      startMonth = new Date().getMonth() + 1;
    }
    else {
      startMonth = 1;
    }
    this.formService.getCreditCardMonths(startMonth).subscribe(
      data => this.creditCardMonths = data
    );
  }

  handleCountryandStateChange(theFormGroup: string) {
    const currentFormGroup = this.checkoutFormGroup.get(theFormGroup);
    const countryCode = currentFormGroup.value.country.code;
    console.log(countryCode)

    this.formService.getStates(countryCode).subscribe(
      data => {
        if (theFormGroup === "shippingAddress") {
          this.shippingAddressStates = data;
        }
        if (theFormGroup === "billingAddress") {
          this.billingAddressStates = data;
        }
        currentFormGroup.get('state').setValue(data[0])
      }
    );
  }


}
