import { ProductCategory } from './../common/product-category';
import { Product } from './../common/product';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root',
})
export class ProductService {
  private baseUrl = '/products';

  constructor(private httpClient: HttpClient) {}

  getProductList(categoryId: number): Observable<Product[]> {
    const searchUrl = `${this.baseUrl}/search/findByCategoryId?id=${categoryId}`;
    return this.httpClient
      .get<GetResponseProduct>(searchUrl)
      .pipe(map((response) => response._embedded.products));
  }

  getProductListPagination(
    thePage: number,
    thePageSize: number,
    categoryId: number
  ): Observable<GetResponseProduct> {
    const searchUrl =
      `${this.baseUrl}/search/findByCategoryId?id=${categoryId}` +
      `&page=${thePage}&size=${thePageSize}`;
    return this.httpClient.get<GetResponseProduct>(searchUrl);
  }

  getProductCategories(): Observable<ProductCategory[]> {
    const categoriesUrl = `/product-category`;
    return this.httpClient
      .get<GetResponseProductCategories>(categoriesUrl)
      .pipe(map((response) => response._embedded.productCategory));
  }

  getProductsByKeyword(keyword: string): Observable<Product[]> {
    const searchWithKeywordUrl = `${this.baseUrl}/search/findByNameContaining?name=${keyword}`;
    return this.httpClient
      .get<GetResponseProduct>(searchWithKeywordUrl)
      .pipe(map((response) => response._embedded.products));
  }
  searchProductsPagination(
    thePage: number,
    thePageSize: number,
    keyword: string
  ): Observable<GetResponseProduct> {
    const searchUrl =
      `${this.baseUrl}/search/findByNameContaining?name=${keyword}` +
      `&page=${thePage}&size=${thePageSize}`;
    return this.httpClient.get<GetResponseProduct>(searchUrl);
  }

  getProduct(productId: number): Observable<Product> {
    const searchWithId = `${this.baseUrl}/${productId}`;
    return this.httpClient.get<Product>(searchWithId);
  }
}

interface GetResponseProduct {
  _embedded: {
    products: Product[];
  };
  page: {
    size: number;
    totalElements: number;
    totalPages: number;
    number: number;
  };
}

interface GetResponseProductCategories {
  _embedded: {
    productCategory: ProductCategory[];
  };
}
