import { CartItem } from './../common/cart-item';
import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class CartService {
  cartItems: CartItem[] = [];
  totalPrice: Subject<number> = new Subject<number>();
  totalQuantity: Subject<number> = new Subject<number>();
  constructor() { }

  addToCart(cartItem: CartItem) {
    console.log('Service Called');
    let alreadyExistInCart: boolean = false;
    let existingCartItem: CartItem = undefined;

    if (this.cartItems.length > 0) {
      for (let tempCartItem of this.cartItems) {
        if (tempCartItem.id === cartItem.id) {
          tempCartItem.quantity += 1;
          existingCartItem = tempCartItem;
          break;
        }
      }

      alreadyExistInCart = existingCartItem != undefined;
    }
    if (alreadyExistInCart) {
      // existingCartItem.quantity++;
    } else {
      console.log('cart item added');
      this.cartItems.push(cartItem);
    }
    this.computeCartTotals();
  }
  computeCartTotals() {
    let totalPriceValue = 0;
    let totalQuantityValue = 0;
    for (let currentCartItem of this.cartItems) {
      totalPriceValue += currentCartItem.quantity * currentCartItem.unitPrice;
      totalQuantityValue += currentCartItem.quantity;
    }

    this.totalPrice.next(totalPriceValue);
    this.totalQuantity.next(totalQuantityValue);
    this.logCartDetails(totalQuantityValue, totalPriceValue);
  }
  logCartDetails(totalQuantityValue: number, totalPriceValue: number) {
    console.log('Cart Details :');
    for (let itemInCart of this.cartItems) {
      let subTotal = itemInCart.quantity * itemInCart.unitPrice;
      console.log(
        `name = ${itemInCart.name} & unitPrice = ${itemInCart.unitPrice} & quantity = ${itemInCart.quantity} & subtotal = ${subTotal}`
      );
    }

    console.log(
      `total Cart Price : ${totalPriceValue} & total Quantity : ${totalQuantityValue}`
    );
  }

  removeProductFromCart(theCartItem: CartItem) {
    const itemIndex = this.cartItems.findIndex(
      (item) => item.id === theCartItem.id
    );
    if (itemIndex > -1) {
      this.cartItems.splice(itemIndex, 1);
      this.computeCartTotals();
    }


  }
  changeQuantityofCartItem(thecartItem: CartItem, quantity: number) {
    if (this.cartItems.length > 0) {
      for (let tempCartItem of this.cartItems) {
        if (tempCartItem.id === thecartItem.id) {
          tempCartItem.quantity = +quantity;
          break;
        }
      }
    }
    this.computeCartTotals();
  }
}
